import { useEffect, useState } from "react"
import { Table } from "antd";
import "./table.css"

const { Column, ColumnGroup } = Table;


export default function MainTable({setSidedata}){

    const [ tableData, setTableData ] = useState();

    function showSidedata(record){
        fetch(`https://raw.githubusercontent.com/skbench/skbench.github.io/main/Json/AES_${record.geometry.split("*")[0]}_${record.geometry.split("*")[1]}_${record.keymode}_${record.mixcolumn}_${record.sboxsecurity.slice(1,-1)}_${record.keyxor}.json`)
        .then(res=>res.json())
        .then(s=>setSidedata(s))
    }

    useEffect(()=>{
        fetch("https://raw.githubusercontent.com/skbench/skbench.github.io/main/Json/table.json")
        .then(res=>res.json())
        .then(s=>setTableData(s))
    },[])

    const geometryFilter=[
        {text:"4*4",value:"4*4"},
        {text:"4*8",value:"4*8"},
        {text:"4*12",value:"4*12"},
        {text:"4*16",value:"4*16"},
        {text:"8*4",value:"8*4"},
        {text:"8*8",value:"8*8"}
    ]

    const keymodeFilter=[
        {text:"SK",value:"SK"},
        {text:"TK1",value:"TK1"},
        {text:"TK2",value:"TK2"},
        {text:"TK3",value:"TK3"}
    ]

    const mixcolumnFilter=[
        {text:"ALMOST",value:"ALMOST"},
        {text:"MDS",value:"MDS"},
        {text:"SKINNY",value:"SKINNY"}
    ]

    const sboxFilter=[        
        {text:"(4_3_-2)",value:"(4_3_-2)"},
        {text:"(8_7_-6)",value:"(8_7_-6)"},
    ]

    const keyxorFilter=[        
        {text:"FULL",value:"FULL"},
        {text:"HALF",value:"HALF"},
    ]
    
    return <div className="table-container">
        <Table dataSource={tableData} pagination={{defaultPageSize:50}} onRow={(record)=>{return {onClick:event=>showSidedata(record)}}}>
            <Column title="Geo." dataIndex="geometry" key="geometry" sorter={(a,b)=>a.geometry>b.geometry?1:a.geometry===b.geometry?0:-1} filters={geometryFilter} onFilter={(value,record)=>value===record.geometry}/>
            <Column title="Key" dataIndex="keymode" key="keymode" sorter={(a,b)=>a.keymode>b.keymode?1:a.keymode===b.keymode?0:-1} filters={keymodeFilter} onFilter={(value,record)=>value===record.keymode}/>
            <Column title="MC" dataIndex="mixcolumn" key="mixcolumn" sorter={(a,b)=>a.mixcolumn>b.mixcolumn?1:a.mixcolumn===b.mixcolumn?0:-1} filters={mixcolumnFilter} onFilter={(value,record)=>value===record.mixcolumn}/>
            <Column title="Sbox" dataIndex="sboxsecurity" key="sboxsecurity" sorter={(a,b)=>a.sboxsecurity>b.sboxsecurity?1:a.sboxsecurity===b.sboxsecurity?0:-1} filters={sboxFilter} onFilter={(value,record)=>value===record.sboxsecurity}/>
            <Column title="Full/Half" dataIndex="keyxor" key="keyxor" sorter={(a,b)=>a.keyxor>b.keyxor?1:a.keyxor===b.keyxor?0:-1} filters={keyxorFilter} onFilter={(value,record)=>value===record.keyxor}/>
            <Column title="Rnds" dataIndex="rounds" key="rounds"/>
            <ColumnGroup title="SK model">
                <Column title="Sbox" dataIndex="sk_sbox_number" key="sk_sbox_number"/>
                <Column title="Deg" dataIndex="sk_degree" key="sk_degree"/>
            </ColumnGroup>
            <ColumnGroup title="TK1 model">
                <Column title="Sbox" dataIndex="tk1_sbox_number" key="tk1_sbox_number"/>
                <Column title="Deg" dataIndex="tk1_degree" key="tk1_degree"/>
            </ColumnGroup>
            <ColumnGroup title="TK2 model">
                <Column title="Sbox" dataIndex="tk2_sbox_number" key="tk2_sbox_number"/>
                <Column title="Deg" dataIndex="tk2_degree" key="tk2_degree"/>
            </ColumnGroup>
            <ColumnGroup title="TK3 model">
                <Column title="Sbox" dataIndex="tk3_sbox_number" key="tk3_sbox_number"/>
                <Column title="Deg" dataIndex="tk3_degree" key="tk3_degree"/>
            </ColumnGroup>
            <Column title="Reference"/>
        </Table>
    </div>
}