import * as ReactDOM from "react-dom/client"
import MainTable from "./table";
import "./index.css"
import { useState } from "react";
import NTUlogo from "./pics/NTU.png"


function Main(){
    const [ sidedata, setSidedata ] = useState();
    const style_NTU_logo={
        position: "absolute",
        left: "13%",
        top: "20px",
        width: "86%"
    };

    function SideDetail(){
        if(sidedata) return <div className="side-details">
            <h4>SR instance:</h4>
            <span>{sidedata.sr}</span>
            <h4>MC instance:</h4>
            <span>{sidedata.mc}</span>
            <h4>Tweakey Schedule Permutation Instance:</h4>
            <span>{sidedata.tspi}</span>
            <h4>Sbox Instance:</h4>
            <span>{sidedata.sbox}</span>
        </div>
        else return <></>
    }

    return(
        <>
            <div className="side-bar">
                <img src={NTUlogo} alt="" style={style_NTU_logo}/>
                {SideDetail()}
            </div>
            <div className="main-content">
                <h1>Differential and Linear Security Data for AES-Like Ciphers</h1>
                <MainTable setSidedata={setSidedata} />
            </div>
        </>
    );
}

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(<Main/>);